	padding-left: 15px;
}

.t {
	display: flex;
	flex-direction: column;
}

.v-icon.v-icon::after {
	content: none !important;
}

.btns {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
}
</style>
