<template>
	<div class="scroll">
		<v-row justify="center">
			<v-expansion-panels :value="panel" multiple focusable>
				<h1 class="page-title">{{maindata.name}}</h1>
				<v-expansion-panel v-for="(item, ind) in tablesdata" :key="item.id" :index="ind">
					<v-expansion-panel-header>{{item.cititle}}</v-expansion-panel-header>
					<v-expansion-panel-content class="content" :value="true">
						<v-card class="mx-auto" flat>
							<v-list-item>
								<v-list-item-content>
									<div class="overline">{{maindata.name}}</div>
									<v-card-text class="news-title mb-1">{{item.cititle}}</v-card-text>
									<v-card-text class="card-t">
										<div v-html="item.fc"></div>
									</v-card-text>
								</v-list-item-content>
							</v-list-item>
						</v-card>
					</v-expansion-panel-content>
				</v-expansion-panel>
			</v-expansion-panels>
		</v-row>
	</div>
</template>

<script>
import { mapState } from 'vuex'
export default {
	name: 'Tables',
	data: () => ({
		panel: [0, 1, 2, 3, 4, 5]
	}),
	computed: {
		...mapState(['assets', 'currentlang']),
		maindata() {
			var data = {
				name: 'Tables',
				childs: [],
				documentsinside: {
					LI: []
				}
			}
			if (typeof this.$store.getters.microsite.childs !== 'undefined') {
				// data = this.$store.getters.microsite.childs[2]
				
				var result = this.$store.getters.microsite.childs.filter(obj => {
  				return obj.refcode === 'tables'
				})
				data = result[0]
			}
			return data
		},
		tablesdata() {
			var itemsfound = []
			this.maindata.documentsinside.LI.forEach(id => {
				var item = this.$store.getters.getCIExtra(id)
				itemsfound.push(item)
			})
			return itemsfound
		}
	},
	watch: {
		/* eslint-disable */
		currentlang(newValue) {
			this.panel = [0, 1, 2, 3, 4]
		}
		/* eslint-enable */
	},
	methods: {},
	mounted() {
		this.$gtag.pageview({
			page_title: 'Tables'
		})
	}
}
</script>

<style lang="scss" scoped>
.scroll {
	height: 100vh;
	// overflow-y: scroll;
	overflow-x: hidden;
	background: linear-gradient(0deg, #bbbdde 0%, white 100%);
}

.news-title {
	font-weight: 500;
	font-size: x-large;
}

.card-t {
	overflow-x: auto;
}

@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
	/* IE10+ CSS styles go here */
	.theme--light.v-expansion-panels .v-expansion-panel {
		top: 50px;
	}
}

.v-expansion-panels:not(.v-expansion-panels--accordion):not(.v-expansion-panels--tile)
	> .v-expansion-panel--next-active
	.v-expansion-panel-header {
	border-bottom-left-radius: 0px !important;
	border-bottom-right-radius: 0px !important;
}

.theme--light.v-expansion-panels.v-expansion-panels--focusable
	.v-expansion-panel-header--active:hover::before,
.theme--light.v-expansion-panels.v-expansion-panels--focusable
	.v-expansion-panel-header--active::before {
	opacity: 0 !important;
}

.theme--light.v-expansion-panels.v-expansion-panels--focusable
	.v-expansion-panel-header:hover::before {
	opacity: 0 !important;
}

.v-expansion-panel-header--active {
	color: white;
	background-color: #9d1f30;
}

.v-responsive {
	max-width: 20%;
}

.page-title {
	flex: 1;
	color: #9d1f30;
}
.v-expansion-panels {
	width: 65%;
	top: 50px;
	margin-bottom: 200px;
}
.v-expansion-panel {
	margin-left: 0%;
}
.content {
	padding-top: 20px;
	flex-direction: row !important;
}

.v-list-item {
	flex-direction: row;
}

.v-expansion-panel-content__wrap {
	display: flex;
	flex-direction: row !important;
	padding: 0 124px 16px !important;
}

.v-card__text {
	padding-left: 0;
	padding-top: 0;
	line-height: 170%;
}

.v-card__actions {
	padding-top: 0;
}

.v-list-item__content {
	padding-bottom: 0;
}

@media only screen and (max-width: 768px) {
	.v-responsive {
		max-width: 100%;
	}

	.news-title {
		font-weight: 500;
		font-size: large;
	}
	.v-expansion-panels {
		width: 85%;
		top: 15px;
	}

	.v-list-item {
		padding: 0px;
	}
}
</style>
